import { Button } from "@chakra-ui/button";
import { useColorMode } from "@chakra-ui/color-mode";
import { Image } from "@chakra-ui/image";
import { 
	useDisclosure, 
	Modal,
	ModalOverlay,
	ModalContent,
	ModalHeader,
	ModalFooter,
	ModalBody,
	ModalCloseButton
} from "@chakra-ui/react";
import { Circle, Stack, Flex, Box, Text } from "@chakra-ui/layout";
import { useMediaQuery } from "@chakra-ui/media-query";


import React from "react";

export const Header = () => {
  const { colorMode } = useColorMode();
  const isDark = colorMode === "dark";
  const { isOpen, onOpen, onClose } = useDisclosure()

//   const [Load, setLoad] = useState('off');
//   const [ButtonValue, setButtonValue] = useState('Click This');

  const [isNotSmallerScreen] = useMediaQuery("(min-width:600px)");
  return (
    <Stack>
      {isNotSmallerScreen && (
        <Circle
          position="absolute"
          bg="#ffc3c3"
          opacity="0.1"
          w="300px"
          h="300px"
          alignSelf="flex-end"
        />
      )}
      <Flex
        direction={isNotSmallerScreen ? "row" : "column"}
        spacing="200px"
        p={isNotSmallerScreen ? "32" : 0}
        alignSelf="flex-start"
      >
        <Box mr={isNotSmallerScreen ? 40 : 0} mt={isNotSmallerScreen ? 0 : 16} align="flex-start">
          <Text fontSize="5xl" fontWeight="semibold">
            Hi, I am
          </Text>
          <Text
            fontSize="7xl"
            fontWeight="bold"
            bgGradient="linear(to-r, #FF69B4, #cb2660, purple.600)"
            bgClip="text"
          >
            Abdul Rahman
          </Text>
          <Text color={isDark ? "gray.200" : "gray.500"}>
            Full Stack Developer and A Student.
          </Text>
	      <Button
		  	boxShadow="dark-lg"
		  	mt={8}
			backgroundColor="#cb2660"
			borderColor="#cb2660"
			color='white'
			_hover={{
				boxShadow: "base",
				// backgroundColor:"transparent",
				// outlineColor: "#cb2660",
				// borderColor: "#cb2660",
				// color: `${isDark ? "white" : "black"}`s
			}}
			_active={{
				boxShadow: "sm",
				// backgroundColor:"transparent",
				// outlineColor: "#cb2660",
				// borderColor: "#cb2660",
				// color: `${isDark ? "white" : "black"}`s
			}}
			_focus={{
				boxShadow: "sm",
				// backgroundColor:"transparent",
				// outlineColor: "#cb2660",
				// borderColor: "#cb2660",
				// color: `${isDark ? "white" : "black"}`s
			}}
			onClick={() => {
				onOpen()
			  }}
		  >
			View Projects
		  </Button>
        </Box>
        <Image
          alignSelf="center"
          mt={isNotSmallerScreen ? "0" : "12"}
          mb={isNotSmallerScreen ? "0" : "12"}
          borderRadius="full"
          backgroundColor="transparent"
          boxShadow="lg"
          boxSize="300px"
          src="https://cdn.discordapp.com/avatars/737553088218529813/4c0efe40e1350a0ea6321b3bcb585535.png?size=2048"
          zIndex={1}
        />
      </Flex>
	  <Modal isCentered blockScrollOnMount={true} finalFocusRef={<Header />} isOpen={isOpen} onClose={onClose}>
        {<OverlayOne />}
        <ModalContent>
          <ModalHeader color="#cb2660" fontWeight="bold">Projects</ModalHeader>
		  <ModalCloseButton />
          <ModalBody>
            <Text>No Projects, Yet.</Text>
          </ModalBody>
          <ModalFooter>
          </ModalFooter>
        </ModalContent>
      </Modal>
    </Stack>
  );
}

const OverlayOne = () => (
	<ModalOverlay
      bg='none'
      backdropFilter='blur(6px)'
    />
)

